
import React from 'react'


function Card(props){
    return(
        <div className="cards">
            <div className="card">
                <img className="ii" src={props.imagesrc} alt="Card-image"/>
                <div className="card-info">
                    <span className="card-category">{props.title}</span>
                    <h3 className="card-title">{props.sname}</h3>
                    <a href="" target="_blank">
                        <button>
                            WATCH NOW 
                        </button>
                    </a>
                </div>
            </div>
        </div>
    )
}

export {Card};